
import router from '@/router';
import { ref, } from 'vue';
import { useStore, } from 'vuex';

export default{
    setup(){

        const store = useStore();

        const title:any = ref('');

        const setNameFilm = ():void => {
            router.push('film');
            store.dispatch('actNameFilm', title.value);
        }

        return { title, setNameFilm, }
    }
}
